<template>
  <div>
    <Table
      :data="bandinfo"
      :header="headers"
      :type="0"
      @move-detail="moveBandDetail"
    />
  </div>
</template>
<script>
import Table from "../Table.vue";
export default {
  components: {
    Table,
  },
  props: ["bandinfo"],
  mounted() {},
  methods: {
    moveBandDetail(value) {
      this.$emit("move-detail", value);
    },
  },

  data: () => ({
    restapi: false,
    search: "",
    nodata: "",
    bandvalue: {
      bandData: {
        start_byte: "",
        sample_count: "",
        fall_detect: "",
        battery_level: "",
        hrConfidence: "",
        spo2Confidence: "",
        hr: "",
        spo2: "",
        motionFlag: "",
        scdState: "",
        activity: "",
        walk_steps: "",
        run_steps: "",
        x: "",
        y: "",
        z: "",
        t: "",
        h: "",
      },
      rssi: -80,
      extAddress: {
        row: 0,
      },
      active: "true",
    },
    itemsPerPageArray: [5, 10, 15, 20],
    page: 1,
    itemsPerPage: 10,
    headers: [
      {
        text: "Status",
        align: "start",
        sortable: true,
        value: "bandvalue.active",
      },
      {
        text: "ID",
        value: "num",
      },
      { text: "Band ID", value: "bid" },
      { text: "Alias", value: "alias" },
      { text: "Name", value: "name" },
      { text: "State", value: "actions", sortable: false },
    ],
  }),
};
</script>
