<template>
  <v-app>
    <v-navigation-drawer
      v-if="$session.user != null"
      :permanent="setDrawerDesign(false)"
      :app="setDrawerDesign(false)"
      :clipped="setDrawerDesign(false)"
      :temporary="setDrawerDesign(true)"
      :absolute="setDrawerDesign(true)"
      class="secondary"
      :width="setDrawerSize()"
      v-model="showDrawer"
      height="100vh"
    >
      <template v-slot:prepend>
        <v-list-item link to="/">
          <v-list-item-content>
            <v-list-item-title
              class="display-1 text-center white--text font-weight-bold"
              >{{ title }}</v-list-item-title
            >
          </v-list-item-content>
        </v-list-item>
      </template>
      <v-list>
        <v-list-item
          v-for="(item, index) in menu_list"
          router-link
          :to="item.to"
          :key="index"
          :class="
            $route.fullPath === item.to
              ? 'point2'
              : item.to !== '/' && $route.fullPath.indexOf(item.to) !== -1
              ? 'point2'
              : 'secondary'
          "
        >
          <v-list-item-content>
            <v-list-item-title class="white--text">{{
              item.name
            }}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>
      <template v-slot:append>
        <div class="pa-2">
          <v-btn depressed block class="point2 white--text" @click="logout()">
            Logout
          </v-btn>
        </div>
      </template>
    </v-navigation-drawer>

    <v-main class="primary">
      <Alert
        :dialogCheck="alertView"
        @dialog-event="dialogEvent"
        :value="value"
      />
      <div v-if="$session.user != null">
        <v-app-bar v-if="isAppBarIcon()" flat class="primary">
          <v-app-bar-nav-icon
            @click.stop="showDrawer = !showDrawer"
          ></v-app-bar-nav-icon
        ></v-app-bar>
        <v-progress-circular
          class="load"
          v-if="$session.load"
          indeterminate
          color="green"
        ></v-progress-circular>
        <router-view
          :band_data="band_data"
          :gateway_data="gateway_data"
          class="router-view"
        />
      </div>

      <login v-else />
    </v-main>
  </v-app>
</template>

<script>
import login from "./components/Login.vue";
import Alert from "./components/Alert.vue";
export default {
  components: { login, Alert },
  async mounted() {
    this.$session.$on("data", async (data) => {
      if (this.$session.user != null) {
        this.band_data = data;
        console.log(data);
      }
    });
    this.$session.$on("gateway_connect", async (data) => {
      console.log(data);
      if (this.$session.user != null) {
        var res = await this.checkUsersGateways(data);
        if (res) {
          if (data.connectstate) {
            if (data.state === 0) {
              this.gatewayAlert(data);
            }
          }

          var index = this.gateway_data
            .map(function(g) {
              return g["panid"];
            })
            .indexOf(data.panid);
          if (index === -1) {
            this.gateway_data.push(data);
          } else {
            this.gateway_data[index] = data;
          }
        }
      }
    });
    this.$session.$on("efwbasync", this.onAlertCheck.bind(this));
  },
  methods: {
    getGatewayState() {
      try {
        this.$session.socket.emit("message", 0);
      } catch (ex) {
        console.log(ex);
      }
    },
    async onAlertCheck(data) {
      if (this.$session.user != null) {
        var bands = null;

        bands = await this.$http.post("bands/permission", {
          uid: this.$session.user.id,
          bid: data.bid,
          permission: this.$session.user.permission,
        });

        if (bands.data.data != null) {
          console.log("감지!!!");
          this.showToast(data);
        }
        // switch (this.$session.user.permission) {
        //   case 0:
        //     this.showToast(data);
        //     break;
        //   case 1:
        //     bands = await this.$http.get(
        //       "users/samegroup/bands/" + this.$session.user.id
        //     );
        //     bands = bands.data.data;
        //     for (i = 0; i < bands.length; i++) {
        //       if (bands[i].bid == data.bid) pass = true;
        //     }
        //     if (pass) this.showToast(data);
        //     break;
        //   case 2:
        //     bands = await this.$http.get(
        //       "users/bandinfo/" + this.$session.user.id
        //     );
        //     bands = bands.data.data;
        //     for (i = 0; i < bands.length; i++) {
        //       if (bands[i].bid == data.bid) pass = true;
        //     }
        //     if (pass) this.showToast(data);
        //     break;
        //   case 3:
        //     break;
        // }
      }
    },
    async checkUsersGateways(data) {
      var res = null;

      switch (this.$session.user.permission) {
        case 0:
          return true;
        default:
          try {
            res = await this.$http.post("usersgateways/check", {
              uid: this.$session.user.id,
              pid: data.panid,
              permission: this.$session.user.permission,
            });

            return res.data.data != null ? true : false;
          } catch {
            return false;
          }
      }
    },
    gatewayAlert(data) {
      this.toastOption.timeout = 0;
      this.$toast.success(
        "[GW] " + data.panid + "가 " + data.ip + "로 연결되었습니다.",
        this.toastOption
      );
    },
    dialogEvent(check) {
      this.alertView = check;
    },
    showToast(data) {
      console.log(data);
      switch (data.type) {
        case 0: {
          console.log("error!!!!!!");
          this.toastOption.timeout = 0;
          this.$toast.error(
            data.bid + " " + this.toastType[data.type],
            this.toastOption
          );

          break;
        }
        case 4: {
          console.log("error!!!!!!");
          this.toastOption.timeout = 0;
          this.$toast.error(
            data.bid + " " + this.toastType[data.type],
            this.toastOption
          );
          break;
        }

        default: {
          this.toastOption.timeout = 5000;
          this.$toast.warning(
            data.bid + " " + this.toastType[data.type],
            this.toastOption
          );
          break;
        }
      }
    },
    isAppBarIcon() {
      switch (this.$vuetify.breakpoint.name) {
        case "xs" || "sm":
          return true;
        default:
          return false;
      }
    },
    isDrawer() {
      switch (this.$vuetify.breakpoint.name) {
        case "xs" || "sm":
          break;
        default:
          this.showDrawer = true;
          break;
      }
      return this.showDrawer;
    },
    logout() {
      this.$session.releaseSession();
    },
    setDrawerSize() {
      switch (this.$vuetify.breakpoint.name) {
        case "xs":
          return "180";
        case "sm":
          return "180";
        case "md":
          return "180";
      }
    },
    setDrawerDesign(type) {
      switch (this.$vuetify.breakpoint.name) {
        case "xs" || "sm":
          return type;
        default:
          return !type;
      }
    },
  },
  data: (vm) => ({
    title: vm.$vuetify.lang.t("$vuetify.efwb.menu.title"),
    showDrawer: true,
    appBarIcon: false,
    startTime: null,
    bandsbattery: {},
    bandsclose: {},
    bandsscd: {},
    viewData: {
      value: true,
    },
    alertView: false,
    value: {
      title: "",
      subtitle: "",
      content: "",
      state: 0,
    },
    windowHeight: null,
    band_data: null,
    gateway_data: [],
    bandlist: [],
    gwstate: false,
    disconnect: 0,
    menu_list: [
      { name: vm.$vuetify.lang.t("$vuetify.efwb.menu.dashboard"), to: "/" },
      { name: vm.$vuetify.lang.t("$vuetify.efwb.menu.band"), to: "/band" },
      {
        name: vm.$vuetify.lang.t("$vuetify.efwb.menu.gateway"),
        to: "/gateway",
      },
      { name: vm.$vuetify.lang.t("$vuetify.efwb.menu.user"), to: "/user" },
      { name: vm.$vuetify.lang.t("$vuetify.efwb.menu.log"), to: "/log" },
    ],
    toastType: [
      "낙상 감지",
      "수신 감도 낮음",
      "배터리 용량 부족",
      "미착용 감지",
      "연결 끊김",
      "심박수 이상",
    ],
    toastOption: {
      position: "top-right",
      timeout: 1000,
      closeOnClick: true,
      pauseOnFocusLoss: true,
      pauseOnHover: true,
      draggable: true,
      draggablePercent: 0.6,
      showCloseButtonOnHover: false,
      hideProgressBar: true,
      closeButton: "button",
      icon: true,
      rtl: false,
    },
  }),
};
</script>
<style lang="scss">
.router-view {
  margin: 30px;
}
.switch_mode_image {
  float: right;
}
.load {
  top: 50%;
  left: 50%;
  position: absolute;
  display: block;
  z-index: 99;
  text-align: center;
  transform: translate(-50%, -50%);
}

// #nav {
//   padding: 30px;

//   a {
//     font-weight: bold;
//     color: #2c3e50;
//   }
// }

/* for MS계열 브라우저 */
@keyframes blink {
  0% {
    color: white;
  }

  50% {
    color: #ff5252;
  }
}

/* for Chrome, Safari */
@-webkit-keyframes blink {
  0% {
    color: white;
  }

  50% {
    color: #ff5252;
  }
}
/* blink CSS 브라우저 별로 각각 애니메이션을 지정해 주어야 동작한다. */
.Vue-Toastification__toast--error > svg {
  animation: blink 1s step-end infinite;
  -webkit-animation: blink 1s step-end infinite;
}
</style>
