<template>
  <div>
    <v-row>
      <v-col cols="12" align="center" sm="3">
        <v-card
          height="30vh"
          class="pt-2"
          style="display: flex; align-items: center; justify-content: center"
        >
          <Time />
        </v-card>
      </v-col>
      <v-col cols="12" sm="5">
        <v-card height="30vh">
          <Weather
            :eventloading="loading"
            @weather-loading="setWeatherLoading"
          />
        </v-card>
      </v-col>
      <v-col cols="12" sm="4">
        <v-card height="30vh">
          <div class="ml-4 pt-2 header-text-style font-weight-bold">
            {{ $vuetify.lang.t("$vuetify.efwb.dashboard.vitalsign") }}
          </div>
          <v-divider class="mt-2" />
          <VitalSign v-if="loading" :eventList="eventList" />
        </v-card>
      </v-col>
    </v-row>
    <v-row>
      <v-col cols="12" md="6">
        <v-card height="60vh">
          <!-- <rssi :band_data="band_data" justify="center" align="center" /> -->
          <div class="ml-4 pt-2 header-text-style font-weight-bold">
            {{ $vuetify.lang.t("$vuetify.efwb.dashboard.activity") }}
          </div>
          <v-divider class="mt-2" />
          <Activity />
        </v-card>
      </v-col>
      <v-col cols="12" md="6">
        <v-row
          ><v-col cols="12" md="12">
            <v-card height="29vh" class="pa-1">
              <div class="ml-4 pt-2 header-text-style font-weight-bold">
                {{ $vuetify.lang.t("$vuetify.efwb.dashboard.falldetect") }}
              </div>
              <v-divider class="mt-2"/>
              <fall-detect height="100vh"/></v-card></v-col
        ></v-row>
        <v-row>
          <v-col cols="12" md="12">
            <v-card height="29vh">
              <div class="ml-4 pt-2 header-text-style font-weight-bold">
                {{ $vuetify.lang.t("$vuetify.efwb.dashboard.event") }}
              </div>
              <v-divider class="mt-2"/>
              <event v-if="loading" :eventList="eventList"/></v-card></v-col
        ></v-row>
      </v-col>
    </v-row>
  </div>
</template>
<script>
import Time from "./Time.vue";
import FallDetect from "./Falldetect.vue";
import Event from "./Event.vue";
import VitalSign from "./Vitalsign.vue";
import Weather from "./Weather2.vue";
import Activity from "./Activity.vue";
// import Rssi from "./Rssi.vue";
// import Map from "./Map.vue";
export default {
  props: ["band_data"],

  components: {
    Time,
    FallDetect,
    Event,
    VitalSign,
    Weather,
    Activity,
  },
  async created() {
    this.$session.load = true;
    this.loadInterval = setInterval(() => {
      if (this.checkLoading()) {
        clearInterval(this.loadInterval);
        this.$session.load = false;
      }
    }, 100);
  },
  async mounted() {
    this.eventList = await this.getEvent();
    this.loading = true;
  },
  methods: {
    async getEvent() {
      try {
        var date = new Date();
        var res = null;
        res = await this.$http.post("events", {
          bid: this.$session.user.permission,
          days: [this.$date.changeDateFormat(date)],
          uid: this.$session.user.id,
        });

        return res.data.data;
      } catch {
        return [];
      }
    },
    setWeatherLoading(loading) {
      this.weatherLoading = loading;
    },
    checkLoading() {
      if (this.loading && this.weatherLoading) return true;
      else return false;
    },
  },
  data() {
    return {
      loadInterval: null,
      headers: [
        {
          text: "번호",
          align: "start",
          sortable: false,
          value: "id",
        },

        { text: "분류", value: "type" },
        { text: "", value: "status" },
      ],
      eventList: [],
      loading: false,
      weatherLoading: false,
    };
  },
};
</script>
<style lang="scss" scoped></style>
