<template>
  <div>
    <apexchart
      :type="type"
      :options="options"
      :series="series"
      :height="setChartHeight()"
    ></apexchart>
  </div>
</template>

<script>
export default {
  async mounted() {
    let result = await this.$http.get("sensordata/activity/dashboard");
    result = result.data.data;
    console.log(result.bid);

    this.options.xaxis.categories.push("0x11012009");
    this.options.xaxis.categories.push("0x11012010");
    this.series.push({ name: "걸음수", data: result.walk });

    this.series.push({ name: "활동시간(분)", data: result.activity });
  },
  methods: {
    setChartHeight() {
      switch (this.$vuetify.breakpoint.name) {
        case "xs":
          return "300vh";
        case "sm":
          return "300vh";
        case "md":
          return "300vh";
        case "lg":
          return "350vh";
        case "xl":
          return "500vh";
      }
    },
  },
  data() {
    return {
      type: "bar",
      options: {
        chart: {
          id: "vuechart-example",
          stacked: true,
          toolbar: {
            show: true,
          },
          zoom: {
            enabled: false,
          },
        },
        dataLabels: {
          enabled: true,
        },
        stroke: {
          width: 1,
        },

        xaxis: {
          labels: {
            style: {
              fontSize: "18px",
              fontFamily: "Helvetica, Arial, sans-serif",
              fontWeight: 400,
              cssClass: "apexcharts-xaxis-label",
            },
          },
          categories: [],
        },
        yaxis: {
          show: true,
        },
        grid: {
          xaxis: {
            lines: {
              show: true,
            },
          },
          yaxis: {
            lines: {
              show: true,
            },
          },
        },
        plotOptions: {
          bar: {
            columnWidth: "40%",
          },
        },
      },
      series: [],
    };
  },
};
</script>
